import React, { useState, useEffect } from "react";
import "./FlipTimer.css";

const AnimatedCard = ({ animation, digit }: any) => {
  return (
    <div className={`flipCard ${animation}`}>
      <span>{digit}</span>
    </div>
  );
};

const StaticCard = ({ position, digit }: any) => {
  return (
    <div className={position}>
      <span>{digit}</span>
    </div>
  );
};

const FlipUnitContainer = ({ data, unit }: any) => {
  let currentDigit: any = data.value;
  let previousDigit: any = data.value + 1;

  if (unit === "minutes" || unit === "seconds") {
    previousDigit = previousDigit === 60 ? 0 : previousDigit;
  }

  if (unit === "hours") {
    previousDigit = previousDigit === 24 ? 0 : previousDigit;
  }

  if (currentDigit < 10) {
    currentDigit = `0${currentDigit}`;
  }
  if (previousDigit < 10) {
    previousDigit = `0${previousDigit}`;
  }

  const digit1 = data.shift ? previousDigit : currentDigit;
  const digit2 = !data.shift ? previousDigit : currentDigit;

  const animation1 = data.shift ? "fold" : "unfold";
  const animation2 = !data.shift ? "fold" : "unfold";

  return (
    <div className={"flipUnitContainer"}>
      <StaticCard position={"upperCard"} digit={currentDigit} />
      <StaticCard position={"lowerCard"} digit={previousDigit} />
      <AnimatedCard digit={digit1} animation={animation1} />
      <AnimatedCard digit={digit2} animation={animation2} />
      <div className={"flipUnitName"}>{unit}</div>
    </div>
  );
};

interface ITimeParams {
  value: number;
  shift: boolean;
}

export const FlipTimer: React.FC<{ endTime: number }> = ({ endTime }) => {
  const [days, setDays] = useState<ITimeParams>({
    value: 0,
    shift: true,
  });

  const [hours, setHours] = useState<ITimeParams>({
    value: 0,
    shift: true,
  });
  const [minutes, setMinutes] = useState<ITimeParams>({
    value: 0,
    shift: true,
  });
  const [seconds, setSeconds] = useState<ITimeParams>({
    value: 0,
    shift: true,
  });

  const updateTime = () => {
    const total = endTime - Date.parse(new Date().toString()) / 1000;
    if (total > 0) {
      const d = Math.floor(total / 86400);
      setDays({
        value: d,
        shift: d === days.value ? days.shift : !days.shift,
      });

      let rest = total - d * 86400;
      const h = Math.floor(rest / 3600);
      rest = rest - h * 3600;
      const m = Math.floor(rest / 60);
      const s = rest - m * 60;

      setHours({
        value: h,
        shift: h === hours.value ? hours.shift : !hours.shift,
      });

      setMinutes({
        value: m,
        shift: m === minutes.value ? minutes.shift : !minutes.shift,
      });

      setSeconds({
        value: s,
        shift: s === seconds.value ? seconds.shift : !seconds.shift,
      });
    }
  };

  useEffect(() => {
    const timer = setInterval(() => {
      updateTime();
    }, 1000);

    return () => clearInterval(timer);
  });

  return (
    <div className={"flipClock"}>
      <FlipUnitContainer unit={"days"} data={days} />
      <FlipUnitContainer unit={"hours"} data={hours} />
      <FlipUnitContainer unit={"minutes"} data={minutes} />
      <FlipUnitContainer unit={"seconds"} data={seconds} />
    </div>
  );
};
